.sidebar {
  border: 1px solid #63a693;
  width: 60%;
  margin-left: auto;
  background-color: #3b8c75;
  color: #fff;
  position: fixed;
  right: 0;
  top: 2.8em;
  bottom: 2.75em;
  overflow-y: scroll;

  &::-webkit-scrollbar {
    width: 4px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #9ad9c7;
  }
}

.sidebar-default {
  list-style-type: none;
  padding-left: 0;
  margin-top: 0.5em;
}

.sb-item {
  padding: 0.2em 0.5em;
  cursor: pointer;

  &:hover {
    background-color: #316c5b;
  }
}

.sidebar-projects {
  .sb-p-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 1.5em;
    border: 1px solid #63a693;
    cursor: pointer;

    &:hover {
      background-color: #9ad9c7;
      color: #316c5b;
    }

    h3 {
      margin: 0;
      padding: 0.2em 0.5em;
    }
  }

  .new-project {
    display: none;
    justify-content: space-between;
    margin: 0.5em 0.2em;

    input {
      border: none;
      padding: 0.2em;
      width: 80%;
      background-color: #bdf2e3;
      font-size: 0.8rem;

      &:focus {
        outline: none;
      }
    }

    i {
      color: #bdf2e3;

      &:hover {
        color: #fff;
      }
    }
  }

  .sb-p-items {
    list-style-type: none;
    padding-left: 0;
    font-size: 0.9rem;
  }
}

@media (min-width: 768px) {
  .sidebar {
    display: block;
    top: 2.4em;
    bottom: 0;
    left: 0;
    right: auto;
    width: 16em;
  }
}
